
#app {
  height: 100%;
}

body {
  background-color: #efeff4;
}
.pageIndex li h2 {
  padding: 0 16px;
}
.pageIndex li h2,
.pageIndex li nav {
  height: 44px;
  line-height: 44px;
}
.pageIndex li nav {
  position: relative;
  padding-left: 16px;
  background-color: white;
  margin-bottom: 1px;
}
.pageIndex li nav::after {
  content: '';
  position: absolute;
  right: 16px;
  top: 50%;
  width: 10px;
  height: 10px;
  border: 1px solid #ddd;
  border-width: 1px 1px 0 0;
  -webkit-transform: rotate(45deg) translateY(-50%);
          transform: rotate(45deg) translateY(-50%);
}
.pageIndex li .block-link {
  display: block;
}

.custom2048-page {
  height: 100%;
  font-family: 'Microsoft YaHei';
  background-color: #faf8ef;
}
.custom2048-page .menu-container {
  width: 300px;
  margin: 0 auto;
  text-align: right;
}
.custom2048-page .block {
  display: inline-block;
  padding: 3px 12px;
  height: 2;
  text-align: center;
  background-color: #8f7a66;
  color: #f9f6f2;
  border-radius: 6px;
  font-size: 18px;
  margin: 5px;
}
.custom2048-page .score-container .block {
  color: rgba(255, 255, 255, 0.6);
  font-size: 14px;
  width: 48px;
  background-color: rgba(143, 122, 102, 0.8);
}
.custom2048-page .score-container .block span {
  display: block;
  color: white;
}
.custom2048-page .restart {
  width: 134px;
  height: 36px;
  line-height: 36px;
  background-color: #d4430f;
}
.custom2048-page .restart:active {
  opacity: 0.8;
}

.Custom2048 canvas {
  position: absolute;
  top: 120px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  display: block;
  border-radius: 6px;
}
@charset "UTF-8";
/*css 初始化 */
html,
body,
ul,
li,
ol,
dl,
dd,
dt,
p,
h1,
h2,
h3,
h4,
h5,
h6,
form,
fieldset,
legend,
img {
  margin: 0;
  padding: 0;
}
fieldset,
img,
input,
button {
  border: none;
  padding: 0;
  margin: 0;
  outline-style: none;
}
/*去掉input等聚焦时的蓝色边框*/
ul,
ol {
  list-style: none;
}
input {
  padding-top: 0;
  padding-bottom: 0;
  font-family: "SimSun", "\5B8B\4F53";
}
select,
input {
  vertical-align: middle;
}
select,
input,
textarea {
  font-size: 12px;
  margin: 0;
}
textarea {
  resize: none;
}
/*防止拖动*/
img {
  border: 0;
  vertical-align: middle;
}
/*  去掉图片低测默认的3像素空白缝隙，或者用display：block也可以*/
table {
  border-collapse: collapse;
}
body {
  font: 12px/150% Arial, Verdana, "\5B8B\4F53";
  color: #666;
  background: #fff;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.clearfix:before,
.clearfix:after {
  /*清楚浮动*/
  content: "";
  display: table;
}
.clearfix:after {
  clear: both;
}
.clearfix {
  *zoom: 1;
  /*IE/7/6*/
}
a {
  color: #666;
  text-decoration: none;
}
a:hover {
  color: #C81623;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  text-decoration: none;
  font-weight: normal;
  font-size: 100%;
}
/*设置h标签的大小，设置跟父亲一样大的字体font-size:100%;*/
s,
i,
em {
  font-style: normal;
  text-decoration: none;
}
.col-red {
  color: #C81623 !important;
}
/*公共类*/
.w {
  /*版心 提取 */
  width: 1210px;
  margin: 0 auto;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
.al {
  text-align: left;
}
.ac {
  text-align: center;
}
.ar {
  text-align: right;
}
.hide {
  display: none;
}
.font12 {
  font-size: 12px;
}
.font14 {
  font-size: 14px;
}
.font16 {
  font-size: 16px;
}
/*公共部分*/
